*,
*:before,
*:after {
  box-sizing: border-box; }

body {
  margin: 0;
  font-family: "TT Lakes";
  font-size: 16px;
  color: #1b1c21;
  background-color: #f8f8f8; }

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer; }

ul,
li {
  display: block;
  padding: 0;
  margin: 0; }

p {
  margin: 0 0 16px; }

a {
  text-decoration: none; }

/* normalize */
html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

hr {
  box-sizing: content-box;
  height: 0; }

abbr[title] {
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
          text-decoration: underline dotted; }

b,
strong {
  font-weight: bolder; }

small {
  font-size: 80%; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  padding: 0.35em 0.75em 0.625em; }

legend {
  box-sizing: border-box;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

progress {
  vertical-align: baseline; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

details {
  display: block; }

summary {
  display: list-item; }

.page-header {
  height: 88px;
  background-color: white;
  display: flex;
  justify-content: space-between;
  padding: 0 var(--h-indent); }
  @media screen and (min-width: 768px) {
    .page-header {
      justify-content: flex-end; } }
  @media screen and (min-width: 1120px) {
    .page-header {
      position: absolute;
      top: 18px;
      right: 0;
      z-index: 1;
      width: calc(100% - 320px);
      margin-left: 0;
      background-color: transparent; } }
  .page-header__block {
    display: flex;
    align-items: center;
    gap: 16px; }
    @media screen and (min-width: 1120px) {
      .page-header__block {
        gap: 32px; } }

@media screen and (min-width: 768px) {
  .header-menu {
    flex-grow: 1; } }

@media screen and (min-width: 1120px) {
  .header-menu {
    display: none; } }

.header-menu__logo {
  display: flex;
  align-items: center; }

@media screen and (max-width: 767.98px) {
  .header-menu__separator {
    display: none; } }

.header-separator {
  width: 2px;
  height: 32px;
  background-color: #eaeaea;
  border-radius: 1px;
  margin: 0 4px; }
  @media screen and (min-width: 1120px) {
    .header-separator {
      display: none; } }

@media screen and (max-width: 767.98px) {
  .header-actions__call,
  .header-actions__chat,
  .header-actions__profile {
    display: none; } }

@media screen and (min-width: 1120px) {
  .header-actions__call,
  .header-actions__chat,
  .header-actions__profile {
    display: none; } }

.header-actions__repair,
.header-actions__status {
  display: flex;
  align-items: center; }

.header-button {
  height: 40px; }
  .header-button__icon {
    height: 40px;
    width: 40px;
    background-color: #ff3e79;
    border-radius: 50%;
    transition: all 0.15s; }
  .header-button__text {
    margin-right: 8px;
    font-weight: 500;
    transition: all 0.15s; }
    @media screen and (max-width: 1119.98px) {
      .header-button__text {
        display: none; } }
  .header-button:hover .header-button__icon {
    background-color: #41F6D7; }
  .header-button:hover .header-button__text {
    color: #41F6D7; }

.main {
  width: 100%; }
  .main__section {
    margin-bottom: 32px; }

.services {
  width: 100%;
  background-color: #f8f8f8; }
  .services__content {
    display: flex;
    flex-wrap: wrap;
    width: 100%; }
    @media screen and (min-width: 768px) {
      .services__content {
        flex-wrap: nowrap; } }
  .services__title-wrapper {
    padding: 24px 0; }
    @media screen and (min-width: 1120px) {
      .services__title-wrapper {
        padding-top: 42px; } }
  .services__title {
    font-size: 28px;
    line-height: 40px;
    margin: 0;
    letter-spacing: -0.6px;
    position: relative;
    padding: 0 var(--h-indent); }
    .services__title::before {
      content: "";
      display: block;
      height: 40px;
      width: 4px;
      background-color: #41f6d7;
      border-radius: 2px;
      position: absolute;
      top: 0;
      left: -2px;
      z-index: 1; }
  .services__text-wrapper {
    padding: 0 var(--h-indent); }
  .services__text {
    font-family: "TT Lakes";
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.2px;
    color: #1b1c21; }
    .services__text--hidden {
      display: none; }
    @media screen and (max-width: 767.98px) {
      .services__text--hidden-mobile {
        display: none; } }
    @media screen and (max-width: 1119.98px) {
      .services__text--hidden-tablet {
        display: none; } }
    .services__text--show {
      display: revert; }
  .services__image {
    width: 100%; }
    @media screen and (min-width: 768px) {
      .services__image {
        min-width: 360px;
        border-radius: 6px;
        margin: 0 var(--h-indent);
        margin-bottom: auto; } }
    @media screen and (min-width: 1120px) {
      .services__image {
        min-width: 532px; } }

.slider {
  position: relative; }
  .slider::after, .slider::before {
    content: "";
    width: 24px;
    height: 40px;
    position: absolute;
    top: 0; }
  .slider::before {
    left: 0;
    background: linear-gradient(90deg, #f8f8f8 17%, rgba(248, 248, 248, 0.0001) 100%); }
  .slider::after {
    right: 0;
    background: linear-gradient(270deg, #f8f8f8 17%, rgba(248, 248, 248, 0.0001) 100%); }
  .slider__wrapper {
    display: flex;
    padding: 0 var(--h-indent);
    margin: 24px 0;
    overflow-x: scroll;
    scrollbar-width: none; }
    .slider__wrapper::-webkit-scrollbar {
      display: none; }
    @media screen and (min-width: 768px) {
      .slider__wrapper {
        margin: 32px 0; } }
    @media screen and (min-width: 1120px) {
      .slider__wrapper {
        margin: 8px 0 12px;
        flex-wrap: wrap; } }
  .slider__item {
    flex-shrink: 0; }
    .slider__item--active .slider__link {
      cursor: default; }
    .slider__item--active .slider__link,
    .slider__item:hover .slider__link {
      border: 2px solid #b8ffec;
      border-radius: 6px;
      background-color: #fff; }
  .slider__link {
    display: block;
    font-weight: 500;
    color: #7e7e82;
    border: 2px solid transparent;
    padding: 8px 16px;
    letter-spacing: -0.2px; }

.more-button {
  font-weight: 500;
  margin-bottom: 16px; }
  .more-button__icon {
    width: 8px;
    height: 12px;
    margin: 0 16px 0 8px; }
  .more-button--active .more-button__icon {
    transform: rotate(180deg); }

.section-title {
  background: linear-gradient(90deg, #F8F8F8, #FFFFFF);
  margin: 0;
  line-height: 24px;
  display: flex;
  align-items: center;
  padding: 16px var(--h-indent);
  font-weight: 400;
  color: #7E7E82;
  border-top: 1px solid #D9FFF5; }

.page-overlay {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: rgba(255, 255, 255, 0.933333);
  top: 0;
  left: 0;
  z-index: 90; }
  .page-overlay--hidden {
    display: none; }

.sidebar-overlay {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: rgba(255, 255, 255, 0.933333);
  top: 0;
  left: 0;
  z-index: 50; }
  .sidebar-overlay--hidden {
    display: none; }

.pill-button {
  height: 32px;
  text-transform: uppercase;
  background-color: #FF3E79;
  color: #FFFFFF;
  padding: 0 16px;
  border-radius: 16px;
  font-size: 12px;
  font-weight: 600;
  display: flex;
  align-items: center;
  line-height: 32px; }
  .pill-button__icon {
    width: 24px;
    height: 24px; }

.sidebar {
  left: -100%;
  top: 0;
  transition: left 0.5s;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  width: 320px;
  min-height: 100vh;
  flex-shrink: 0;
  z-index: 90;
  grid-row: 1 / 3;
